import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import '../styles/profilepage.css';

const ProfilePage = () => {
    const { slug } = useParams(); // Get the slug from the URL
    const [profileData, setProfileData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const fetchProfileData = async () => {
            try {
                // Fetch user profile data with posts
                const response = await fetch(`/api/users/${slug}`);
                if (!response.ok) {
                    throw new Error(`HTTP error! status: ${response.status}`);
                }

                const data = await response.json();
                setProfileData(data);
            } catch (err) {
                console.error('Error fetching profile data:', err.message);
                setError('Could not fetch profile data. Please try again later.');
            } finally {
                setLoading(false);
            }
        };

        fetchProfileData();
    }, [slug]);

    if (loading) {
        return <p>Loading profile...</p>;
    }

    if (error) {
        return <p className="error-message">{error}</p>;
    }

    if (!profileData) {
        return <p>No profile data found.</p>;
    }

    return (
        <div className="profile-page">
            <h1>{profileData.displayName || 'No Display Name'}</h1>
            <p>@{profileData.username}</p>
            <p>{profileData.bio || 'This user has no bio.'}</p>

            <div className="post-list">
                {profileData.posts && profileData.posts.length === 0 ? (
                    <p>No posts by this user.</p>
                ) : (
                    <div className="post-grid">
                        {profileData.posts.map((post) => (
                            <div key={post.id} className="post-item">
                                <Link to={`/users/${slug}/posts/${post.id}`}>
                                    <div className="post-image-container">
                                        <img
                                            src={post.image}
                                            alt={post.description || 'Post image'}
                                            className="post-image"
                                        />
                                        <div className="post-overlay">
                                            <p className="post-description">{post.description || 'No description available.'}</p>
                                        </div>
                                    </div>
                                </Link>
                            </div>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
};

export default ProfilePage;
